@import "../../variables";

.about {
  padding: 15rem 0rem;

  background-color: #fff;

  &__container {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10rem;
  }

  &__img {
    background-image: url(../../images/about-img.62b47e7f183d4b4e9feb.webp);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    display: block;
    border-radius: 1.7rem;
    height: 35rem;
    width: 41rem;
    position: relative;
  }

  &__rotating {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    width: 19rem;
    height: 19rem;
    position: absolute;
    border-radius: 50%;
    bottom: -7rem;
    right: -7rem;

    img:nth-child(1) {
      width: 6rem;
      height: 6rem;
    }

    img:nth-child(2) {
      width: 19rem;
      height: 19rem;
      position: absolute;
      -webkit-animation: rotate 9s linear infinite;
      animation: rotate 9s linear infinite;
    }
  }

  &__content {
    flex: 0 0 50%;

    h3 {
      color: $blue;
      font-size: 1.7rem;
      font-weight: 700;
      margin-bottom: 1rem;
      text-transform: uppercase;
    }

    h2 {
      color: $off-black;
      font-family: Poppins, sans-serif;
      font-size: 2.5rem;
      line-height: 1.4;
      margin-bottom: 2rem;
    }

    p {
      color: $gray;
      font-family: Mulish, sans-serif;
      font-size: 1.7rem;
      font-weight: 500;
      line-height: 1.5;
    }

    p:not(p:last-child) {
      margin-bottom: 1rem;
    }
  }
}

@keyframes rotate {
  100% {
    -webkit-transform: rotate(1turn);
    transform: rotate(1turn);
  }
}

@media screen and (max-width: 1020px) {
  .about {
    &__container {
      flex-direction: column;
      width: 70%;
      gap: 8rem;

      h3 {
        margin-bottom: 2rem;
      }

      h2 {
        margin-bottom: 3rem;
      }

      p:not(p:last-child) {
        margin-bottom: 2rem;
      }
    }

    &__content {
      text-align: center;
    }
  }
}

@media screen and (max-width: 750px) {
  .about {
    &__rotating {
      display: none;
    }
  }
}

@media screen and (max-width: 550px) {
  .about {
    &__container {
      width: 100%;
    }

    &__img {
      width: 90%;
      background-position: center;
    }
  }
}
